<template>
  <view>
    <!-- 自定义导航 -->
    <cu-custom bgColor="bg-gradual-blue" :isBack="false">
        <block slot="content">考试中心</block>
    </cu-custom>
    
    <!-- tab栏 -->
    <scroll-view class="exam-nav" scroll-x  scroll-with-animation :scroll-left="scrollLeft">
        <view class="exam-nav__item mh10 ph20" 
            v-for="(item,index) in navTop"
            :class="index == TabCur?'exam-nav__item--active':''"
            :key="index"
            @tap="tabSelect"
            :style="[{ animation: 'show ' + ((index + 1) * 0.2 + 1) + 's 1' }]"
            :data-id="index">
            {{item.title}}
        </view>
    </scroll-view>

    <!-- 考试详情 -->
    <view class="exam-content" v-if="isLogin && TabCur === 0">
        <view v-for="item in examPaperList" :key="item.id" class="exam-content__item fz30 animation-slide-right">
            <view class="exam-content__item-title ph30">
                <text>{{ item.paperName }}</text>
                <text class="exam-content__item-title-time fz23">{{ item.suggestTime }}分钟</text>
            </view>
            <view class="exam-content__item-info ph30">
                <text>总数:{{ item.paperCount }}</text>
                <text>总分:{{ item.paperScore }}</text>
                <text>次数:{{ item.remainJoinNum }}</text>
            </view>
        </view>
    </view>

    <view class="exam-content" v-else-if="isLogin && TabCur === 1">
        <view v-for="item in answerList" :key="item.id" class="exam-content__item fz30 animation-slide-right">
            <view class="exam-content__item-title ph30">
                <text>{{ item.paperName }}</text>
                <text class="exam-content__item-title-time fz23">{{ item.userScore }}分</text>
            </view>
            <view class="exam-content__item-info ph30">
                <text>总数:{{ item.paperCount }}</text>
                <text>总分:{{ item.paperScore }}</text>
                <text>提交:{{ item.gmtModified }}</text>
            </view>
        </view>
    </view>

    <view style="text-align: center;" v-else>
        <image style="width:300rpx; height:300rpx; margin-top: 100rpx" src="/static/ASCElogo.png" />
    </view>
	
    <!-- 底部撑开块 -->
    <view style="height: 200rpx;width: 1rpx;"></view>
  </view>
</template>

<script>
import * as ApiExam from '@/api/exam'
import { checkLogin } from '@/utils/app';
export default {
    data() {
			return {
                // 是否已登录
                isLogin: false,
                // 考试数据
                examPaperList: [],
                // 考试记录
                answerList: [],
                // tab选项索引
				TabCur: 0,
                // 滑动距离
				scrollLeft: 0,
                // tab选项
				navTop:[
					{
						id: 1,
						title: '考试中心'
					},
					{
						id: 2,
						title: '考试记录'
					},
					{
						id: 3,
						title: '智能训练'
					},
                    {
						id: 4,
						title: '固定试卷'
					},
					{
						id: 5,
						title: '班级试卷'
					},
					{
						id: 6,
						title: '系级试卷'
					}
				]
			};
		},

        /**
         * 监听页面加载
         */
        created() {
            this.onRefreshPage()
        },

        methods: {
            /**
             * 刷新页面
             */
            onRefreshPage() {
                // 判断是否已经登录
                this.isLogin = checkLogin();
                // 获取页面数据
                this.getPageData();
            },

            /**
             * 获取页面数据
             */
            getPageData() {
                this.getAllExam()
            },

            /**
             * 获取所有的试卷
             */
            getAllExam() {
                this.isLogin && ApiExam.getAllExamInfo()
                    .then(result => {
                        this.examPaperList = result.data.examPaperList
                    })
                    .catch(err => {
                        this.isLogin = false
                        reject(err)
                    })
            },

            /**
             * 查询考试记录
             */
            getRecodeScore() {
                // return new Promise((resolve, reject) => {
                this.isLogin && ApiExam.selectAllExamPaperAnswer()
                    .then(result => {
                        this.answerList = result.data.answerList
                    })
                    .catch(err => {
                        this.isLogin = false
                        reject(err)
                    })
                // })
            },

            /**
             * 切换tab选项
             */
            tabSelect(e) {
                console.log(1111, e)
                let ne = e.currentTarget ? e.currentTarget.dataset.id : e;
				this.TabCur = ne;
                this.scrollLeft = (ne - 1) * 60
                if (this.TabCur === 0) {
                    this.getAllExam()
                } else if (this.TabCur === 1) {
                    this.getRecodeScore()
                }
			},
        },


        /**
         * 下拉刷新
         */
        onPullDownRefresh() {
            this.tabSelect(this.TabCur)
            uni.stopPullDownRefresh()
        }
}
</script>

<style lang="scss" scoped>
@import "@/styles/settings/var.scss";
@import "@/styles/tools/index.scss";

@include b(exam-nav) {
    background-color: $background-color-secondary;
    white-space: nowrap;
    @include e(item) {
        height: 90rpx;
        display: inline-block;
        line-height: 90rpx;
        @include m(active) {
            border-bottom: 4rpx $border-style-base $color-text-quaternary;
        }
    }
}


@include b(exam-content) {
    
    @include e(item) {
        display: block;
        overflow: hidden;
        border-radius: 8rpx;
        margin: 30rpx;
        padding: 30rpx 30rpx 0 30rpx;
        background-color: $background-color-secondary;
    }
    
    @include e(item-title) {
        color: $color-text-secondary;  //secondary
        height: 70rpx;  
        @include box-center($justify: space-between);
        border-bottom: 1px $border-width-base $color-transparent-black;
    }

    @include e(item-info) {
         height: 100rpx;
        @include box-center($justify: space-between);
        color: #666666;
    }

    @include e(item-title-time) {
        color: $color-text-quaternary;
        background-color: rgba(58, 142, 230, .1);
        border-radius: 30rpx;
        display: block;
        min-width: 70rpx;
        width: 60rpx;
        height: 32rpx;
        line-height: 32rpx;
        text-align: center;
    }
}


</style>